  .gallery {
    width: 1600px;
  }
}

.gallery-box {
  flex-wrap: wrap;
  margin-top: 15px;
  margin-bottom: -30px;
  margin-left: -30px;
  display: flex;
}

.photo-card {
  flex-basis: 100%;
  margin-bottom: 30px;
  margin-left: 30px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
